body {
    font: 20px "Century Gothic", Futura, sans-serif;
    margin-top: 50px;
    margin-left: 200px;
    background-image: url(https://th.bing.com/th/id/R.642fecbcea63dac4ee46b4161575b4f2?rik=P%2bp0CpuwTfNedg&riu=http%3a%2f%2fwww.pptbackgrounds.org%2fuploads%2ftic-tac-toe-backgrounds-wallpapers.jpg&ehk=pEa%2bzIERJvzkLrHGGaFxo6l3P6K7VZh2EFQCDU2YkWY%3d&risl=&pid=ImgRaw&r=0);
    background-size: cover;
}
  
  ol, ul {
    padding-left: 20px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 3px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 50px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 50px;
  }
  
  .square:focus {
    outline: none;
    
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
    
  }
  
  .game {
    display: flex;
    flex-direction: row;
    /* Align items make box go down with history. Don't use */
    /* align-items: center; */
  }
  
  .game-info {
    margin-left: 50px;
    
  }

  h1 {
      margin-left: 70px;
      margin-bottom: 50px;
      text-shadow: 4px 4px #ddd;
  }